import React, { useState } from "react"

import "../styles/normalize.css";
import "../styles/bulma.scss";
import "../styles/layout.scss";
import leftArrow from "../../static/left-arrow.svg";
import hamburger from "../../static/hamburger.svg";
import SwipeableViews from "react-swipeable-views"

const Layout = ({children}) => {

  const lastIndex = children.length - 1;


  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);

  return (
    <div className="layout">
      <div className="header">
        <div>
          <progress class="progress is-primary" value={activeIndex} max={lastIndex + 1} >15%</progress>
        </div>
        <div className="header__buttons"> 
          <img 
            onClick={() => {
              if(activeIndex>0){
                setActiveIndex(i => i-1)}
              }
            }
            className="header_back--arrow" 
            src={leftArrow} 
          />
      
          <img
            onClick={() => setModalIsOpen(true)}
            className="header_hamburger" src={hamburger} 
          />
        </div>
      </div>
      <main>
        <form>
          <SwipeableViews index={activeIndex} onChangeIndex={(i) => setActiveIndex(i)} > 
        
              {children}

          </SwipeableViews>
        </form>
      </main>

      <div class={`modal ${modalIsOpen && 'is-active'}`} >
            <div class="modal-background"></div>
            <div class="modal-content">fafkjaskj afkjkjakj ajfjfkj
                <div className="box">
                <h2> etataearfsdasrt test </h2> 
                <h3> asdasdsadadfaf </h3>
                </div>
            </div>
            <button
              onClick={() => setModalIsOpen(false)} 
              class="modal-close is-large" 
              aria-label="close"></button>
      </div>
   

      <div className="footer"> 
        <button 
          onClick={() => {
            if(activeIndex<lastIndex){
              setActiveIndex(i => i+1)
            }
          }}
          className="button is-primary footer_button--continue"> Continue </button>
      
      </div>      
    </div>
  )
}

export default Layout;